@import url(https://fonts.googleapis.com/css?family=Inconsolata|Roboto:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*:before,
*:after {
  margin: 0;
  box-sizing: border-box; }

a,
button {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #0186f6; }
  a:hover {
    color: #9effff; }

.btn,
a {
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out; }
  .btn:active, .btn:focus, .btn:hover,
  a:active,
  a:focus,
  a:hover {
    outline: 0;
    box-shadow: none; }

/**
 * Basic styles for input fields
 */
input[type="email"],
input[type="password"],
input[type="text"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-focus-ring-color: none;
  outline: none; }
  input[type="email"]:active, input[type="email"]:focus, input[type="email"]:hover,
  input[type="password"]:active,
  input[type="password"]:focus,
  input[type="password"]:hover,
  input[type="text"]:active,
  input[type="text"]:focus,
  input[type="text"]:hover {
    box-shadow: none; }

input,
select,
button,
textarea {
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }
  input::-webkit-outer-spin-button, input::-webkit-inner-spin-button,
  select::-webkit-outer-spin-button,
  select::-webkit-inner-spin-button,
  button::-webkit-outer-spin-button,
  button::-webkit-inner-spin-button,
  textarea::-webkit-outer-spin-button,
  textarea::-webkit-inner-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none; }

body {
  color: #5a5a5a;
  font: normal 125%/1.4 "Roboto", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif; }

header {
  padding: 60px;
  font-family: "Inconsolata", "Courier New", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Monaco", monospace;
  font-size: 62px;
  color: #ffffff;
  text-align: center;
  background-color: #0d3a58; }

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0;
  background: #193549;
  color: #ffffff;
  font-family: "Inconsolata", "Courier New", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Monaco", monospace; }

.hljs-comment,
.hljs-quote {
  color: #0186f6; }

.hljs-addition,
.hljs-selector-class {
  color: #30d403; }

.hljs-number,
.hljs-string,
.hljs-meta .hljs-meta-string,
.hljs-literal,
.hljs-doctag,
.hljs-regexp {
  color: #fbea7d; }

.hljs-title,
.hljs-section,
.hljs-name,
.hljs-selector-id,
.hljs-selector-tag {
  color: #9effff; }

.hljs-attribute,
.hljs-attr,
.hljs-template-variable,
.hljs-class .hljs-title,
.hljs-type {
  color: #8fe783; }

.hljs-variable {
  color: #9effff; }

.hljs-symbol,
.hljs-bullet,
.hljs-subst,
.hljs-meta,
.hljs-meta,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-link,
.hljs-keyword {
  color: #ff9d00; }

.hljs-built_in,
.hljs-deletion {
  color: #dc322f; }

.hljs-formula {
  background: #0d3a58; }

.hljs-emphasis {
  font-style: italic; }

.hljs-strong {
  font-weight: bold; }

.container {
  margin: 0 auto;
  width: 100%; }

.back-to-top {
  position: fixed;
  bottom: 25px;
  right: 25px;
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 75px;
  cursor: pointer; }

.caret {
  margin-bottom: 5px;
  width: 25px; }

.cat {
  width: 100%; }

.hash {
  opacity: 0;
  pointer-events: none;
  -webkit-transition: .2s;
  transition: .2s; }
  .hash--lg {
    margin-left: 15px; }
  .hash--sm {
    margin-left: 3px; }

.hash-parent:hover .hash {
  opacity: 1;
  pointer-events: auto; }

.summary-list {
  list-style-position: inside; }

.section {
  display: -webkit-flex;
  display: flex;
  background-color: #193549;
  color: #e2dcde;
  border-top: 1px #15232d solid; }

.section-flex {
  display: -webkit-flex;
  display: flex; }

.category {
  padding: 20px 0;
  font-size: 36px;
  background-color: #122736; }

.base-width {
  margin: 0 auto;
  padding: 0 40px;
  width: 1280px; }

.rule {
  -webkit-flex: 0 0 50%;
          flex: 0 0 50%;
  padding: 40px 40px 40px 0;
  font-size: 20px; }

.rule-monocol {
  padding: 40px;
  font-size: 20px; }

.sample {
  -webkit-flex: 0 0 50%;
          flex: 0 0 50%;
  padding: 40px 0 40px 40px;
  overflow: hidden;
  font-size: 20px; }

::-webkit-scrollbar {
  width: 10px;
  height: 10px; }

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px #122736;
  border-radius: 10px; }

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #26516f;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5); }

